<template>
  <div>
    <template></template>
    <div id="container" >
      <SideBar v-if="this.loginUser"></SideBar>
      <div id="rightSide">
        <div id="header" v-if="this.loginUser">
          <MainNavBar></MainNavBar>
        </div>
        <div id="content">
          <router-view></router-view>
        </div>
      </div>
    </div>
  </div>


</template>
<script>

  import navbar from "./components/teamModule/navbar";
  import userInfo from "./components/userInfo";
  import teamOperate from "./components/teamModule/teamOperate";
  import store from './store/store'
  import {mapState, mapMutations} from 'vuex'
  import MainNavBar from "./components/MainNavBar";
  import SideBar from "./components/SideBar";
  import Login from "./components/Login";

  export default {
    name: 'App',
    components: {navbar, userInfo, teamOperate, MainNavBar, SideBar, Login},
    store,
    data() {
      return {
        userInfoToOpe: null,//传给组件的值,通过create钩子函数初始化,这里要写成null,不能写成{},否则会被判为true
        loginUser: "",
      }
    },
    watch: {
      loginUser() {
        var self = this;

        function getUserInfo() {
          return self.axios({
            method: "post",
            url: "/userInfo",
            params: {
              userId: self.loginUser//得改
              // userId: "1601"//得改
            }
          })
        }

        function getTeamInfo() {
          return self.axios({
            method: "get",
            url: "/search",
            params: {
              leader: self.loginUser//得改
            }
          })
        }

        self.axios.all([getUserInfo(), getTeamInfo()])
          .then(self.axios.spread(function (acct, perms) {
            //当这两个请求都完成的时候会触发这个函数，两个参数分别代表返回的结果
            self.initUserInfo(acct.data);
            if (perms.data) {
              self.updateTeam(perms.data);
            }
            console.log(perms.data)
            self.userInfoToOpe = self.userInfoData
          }))
          .catch(err => {
            console.log(err)
          })
      }
    },
    computed: {
      ...mapState(['userInfoData'])
    },
    methods: {
      ...mapMutations(['initUserInfo', "updateTeam"])
    },
    mounted() {
      this.loginUser = localStorage.token
      var self = this
      if (this.loginUser) {
        function getUserInfo() {
          return self.axios({
            method: "post",
            url: "/userInfo",
            params: {
              userId: self.loginUser//得改
              // userId: "1601"//得改
            }
          })
        }

        function getTeamInfo() {
          return self.axios({
            method: "get",
            url: "/search",
            params: {
              leader: self.loginUser//得改
            }
          })
        }

        self.axios.all([getUserInfo(), getTeamInfo()])
          .then(self.axios.spread(function (acct, perms) {
            //当这两个请求都完成的时候会触发这个函数，两个参数分别代表返回的结果
            self.initUserInfo(acct.data);
            if (perms.data) {
              self.updateTeam(perms.data);
            }
            localStorage.teamleader= self.userInfoData.teamleader
            self.userInfoToOpe = self.userInfoData//更改vuex里面的数据据
          }))
          .catch(err => {
            localStorage.token=""
          })
      }else {
        location.href="#/login"
      }
    }
  }
</script>

<style>
  * {
    margin: 0;
    padding: 0;
  }


  #app {
    width: 100%;
    height: 500px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }

  #info {
    background-color: #5b5a57;
    width: 240px;
    padding: 20px;
  }

  #right {
    overflow: hidden;
    flex: 1;
  }

  #nav {
    /*background-color: #0bfffc;*/
    /*height: 50px;*/
  }

  /*#view {*/
  /*  overflow: auto;*/
  /*}*/

  .formInput {
    margin-bottom: 10px;
  }

  thead {
    font-weight: bold;
  }

  #container {
    display: flex;
    flex-direction: row;
  }

  #sidebar {
    max-width: 250px;
  }

  #rightSide {
    height: 850px;
    flex: 1;
  }
</style>



